.card{
    

}
@media only screen and (min-width:768px){
    div.cardbox{
        float:left;
        margin-left:3vw;
        margin-bottom: 3vw;
        border-radius: 10px;
        width: 30%;

    }
   

    .card{
        border-radius: 15px;

    }
  }
  @media only screen and (min-width:480px) and (max-width:768px) {
    div.cardbox{
        padding: 8vw;
        margin-bottom: 2vw;
        align-items: center;
        border-radius: 5px;
        width: 80%;
    }
    .card{
        border-radius: 15px;
        
    }
  }
  @media only screen and (max-width:479px){
    div.cardbox{
        padding: 8vw;
        margin-bottom: 2vw;
        align-items: center;
        width: 90%;

    } 
    .card{
        border-radius: 15px;
      
    }
  }